.App {
  font-family: 'Montserrat', sans-serif;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-size: 2em;
}




/* Standard Tags */
.standard-img {
  width: 100%;
}

.anno-standard {
  font-size: 0.8em;
  font-weight: 300;
  margin-bottom: 10%;
}

.standard-button {
  color: black;
  text-decoration: none;
  padding: 2% 4% 2% 4%;
  background-color: white;
  margin-top: 3%;
}
/* Standard Tags ENDS */









/* Standard NAV STARTS */
.standard-nav {
  text-align: center;
  width: 100%;
  background-color: #1a1a1a;
  padding: 2% 0% 2% 0%;
}

.standard-nav-a {
  text-decoration: none;
  color: white;
  margin-right: 2%;
  position: relative;
}

.standard-nav-a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.standard-nav-a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

/* Standard NAV ENDS */






/* Splash Starts */
.splash {
  height: 100vh;
  /* width: 100%; */
  background-color: #1a1a1a;
  /* background-image: url("../media/splash.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; */
  overflow: hidden;
  -webkit-box-shadow: 10px 10px 92px 6px rgba(0,0,0,0.25);
  -moz-box-shadow: 10px 10px 92px 6px rgba(0,0,0,0.25);
  box-shadow: 10px 10px 92px 6px rgba(0,0,0,0.25);
}

/* Slide Show */
* {box-sizing:border-box}

/* Slideshow container */
.slideshow-container {
  display: flex;
  position: relative;
  margin: auto;
  margin-top: 10px;
}

.slide-img {
  width: 100vw;
  height: 60vh;
}

.canvas {
  height: 70vh;
}

/* Mobile Optimization */
@media only screen and (max-width: 600px) {
  .canvas {
    height: 100vh;
  }
}
/* Splash ENDS */









/* Index STARTS */
.index-container {
  margin: 5% 15% 5% 15%;
  overflow: auto;
}

.index-col-one {
  width: 55%;
  float: left;
}

.index-col-two {
  width: 40%;
  float: right;
}

/* Mobile Optimization */
@media only screen and (max-width: 600px) {
  .index-col-one {
    width: 100%;
  }
  .index-col-two {
    width: 100%;
  }
}
/* Index ENDS */




/* About STARTS */
.about-col-left {
  width: 45%;
  float: left;
}

.about-col-right {
  width: 45%;
  float: right;
}

.about-text {
  margin-top: 30%;
}

/* Mobile Optimization */
@media only screen and (max-width: 600px) {
  .about-col-left {
    width: 100%;
    float: left;
  }

  .about-col-right {
    width: 100%;
    float: left;
  }
  .about-text {
    margin-top: 2%;
  }
}
/* About ENDS */








/* Shop STARTS */
.shop-container {
  margin-bottom: 3%;
}

.shop-item {
  width: 30%;
  margin: 0% 1.5% 1.5% 1.5%;
  float: left;
  cursor: pointer;
}

.shop-headers {
  text-align: center;
  text-decoration: underline;
}

.shop-sub-headers {
  margin-left: 1.5%;
  text-decoration: underline;
}

.overlay-img {
  width: 40%;
  height: auto;
  object-fit: contain;
}

.overlay-content {
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  padding: 1em;
  text-align: center;
}

/* Mobile Optimization */
@media only screen and (max-width: 600px) {
  .shop-item {
    width: 100%;
  }
}
/* Shop ENDS */



/* Contact STARTS */
.contact-form {
  text-align: center;
}

.contact-input {
  margin-bottom: 2%;
  border: none;
  background-color: #1a1a1a;
  padding: 2% 3% 2% 3%;
  width: 30%;
  border-radius: 1em;
  color: white;
}

.contact-input::placeholder {
  color: white;
}

.contact-sub-button {
  margin-bottom: 2%;
  border: none;
  background-color: #1a1a1a;
  padding: 1% 2% 1% 2%;
  width: 10%;
  border-radius: 1em;
  color: white;
}

/* Mobile Optimization */
@media only screen and (max-width: 600px) {
  .contact-input {
    width: 100%;
    font-size: 1.8em;
    rows: 2;
  }
  .contact-sub-button {
    margin-bottom: 2%;
    border: none;
    background-color: #1a1a1a;
    padding: 1% 2% 1% 2%;
    width: 50%;
    border-radius: 1.5em;
    color: white;
  }
}
/* Contact ENDS */





/* Footer */
.footer {
  padding: 2% 15% 2% 15%;
  background-color: #1a1a1a;
  text-align: center;
  color: white;
}

.footer-nav {

}

.footer-nav-a {
  color: white;
  text-decoration: none;
  margin-right: 3%;
}

/* Footer ENDS */



/* Animations STARTS */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Styles for overlay component */

.overlay {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.75);
  display: flex;
  justify-content: center;
  align-items: center;
}
